import {
  selectDetailProject,
  selectSceneSelected,
} from '@containers/Home/selectors';
import dynamic from 'next/dynamic';
import { memo } from 'react';
import { useSelector } from 'react-redux';
import { HeaderWrapper } from './styled';

const Logo = dynamic(() => import('@containers/Home/components/Logo'));

const Header = memo(() => {
  const detailProject = useSelector(selectDetailProject);
  const sceneSelected = useSelector(selectSceneSelected);

  return (
    <HeaderWrapper id="themeHeader">
      <Logo />

      <div className="mainTitle">
        <div className="--content">
          <h1 className="--projectName">{detailProject?.title || 'N/A'}</h1>
          <span className="--sceneName">{sceneSelected?.title || 'N/A'}</span>
        </div>
      </div>
    </HeaderWrapper>
  );
});

export default Header;
